body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

header {
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.img_link {
  width: 40px;
  height: 40px;
}

.nav__item {
  text-transform: uppercase;
  height: 60px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  white-space: nowrap;
  color: #000;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  margin-left: 0;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
